import React, { useEffect, useRef } from "react";
import { CardSvg, PhoneSvg, TagSvg } from "../lib/svgs";
import styled from "styled-components";
import { useEhState } from "@foursk/eh-react-hooks";
import { sTheme } from "../lib/theme";
import { timedAnimation, withEhStates } from "../lib/utils";
import { sDimensions } from "../style/responsive";
import { Animated } from "react-native";

const Container = styled.div`
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
`;

// Have to avoid ph due to anim
const Spacer = withEhStates(
	styled.div`
		height: 16px;
	`,
	sDimensions
);

export const TagScanAnimation = () => {
	const { theme } = useEhState(sTheme);
	const phoneAnim = useRef(new Animated.Value(0)).current;
	const phoneAnimInterp = phoneAnim.interpolate({
		inputRange: [0, 1],
		outputRange: [0, -170],
	});

	useEffect(() => {
		Animated.loop(
			Animated.sequence([
				timedAnimation(phoneAnim, 1, 2250),
				timedAnimation(phoneAnim, 1, 750),
				timedAnimation(phoneAnim, 0, 2250),
				timedAnimation(phoneAnim, 0, 750),
			])
		).start();
	}, []);

	return (
		<Container>
			<Spacer />
			<CardSvg size={100} color="#f52d56" />
			<Spacer />
			<Animated.View
				style={{
					zIndex: 5,
					marginLeft: 10,
					transform: [{ translateY: phoneAnimInterp }],
				}}
			>
				<PhoneSvg size={200} color={theme.colors.aBlack} />
			</Animated.View>
		</Container>
	);
};
