import React, { useEffect, useRef } from "react";
import { View } from "react-native";
import { withEhStates } from "../lib/utils";
import { sTheme } from "../lib/theme";
import styled from "styled-components";
import { sDimensions } from "../style/responsive";
import { Animated } from "react-native";
// import { h2 } from '../style/theme';

const LoadElement = withEhStates(
	styled(Animated.View)`
		margin-top: ${({ marginTop }) => marginTop || "8px"};
		/* padding-top: ${({ paddingTop }) => paddingTop || "0px"}; */
		background-color: blue;
		width: ${({ width }) => width || "auto"};
		height: ${({ height }) => (height ? height : "auto")};
		margin-bottom: ${({ marginBottom }) =>
			marginBottom ? marginBottom : "0px"};
		margin-right: ${({ marginRight }) => (marginRight ? marginRight : "0px")};
		align-self: ${({ alignSelf }) => alignSelf || "flex-start"};
	`,
	sTheme,
	sDimensions
);

function animationHandler(anim, from, to) {
	return Animated.loop(
		Animated.sequence([
			Animated.timing(anim, {
				toValue: from,
				duration: 1000,
				useNativeDriver: true,
			}),
			Animated.timing(anim, {
				toValue: to,
				duration: 1000,
				useNativeDriver: true,
			}),
		])
	);
}

export function LoadingElement({
	marginTop,
	paddingTop,
	width,
	height,
	marginBottom,
	marginRight,
	alignSelf,
	children,
	hasValue,
}) {
	const pulseAnim = useRef(new Animated.Value(0.6)).current;

	useEffect(() => {
		animationHandler(pulseAnim, 0.2, 0.6).start();
	}, []);

	if (hasValue) {
		return children;
	} else {
		return (
			<LoadElement
				marginTop={marginTop}
				paddingTop={paddingTop}
				width={width}
				height={height}
				marginBottom={marginBottom}
				marginRight={marginRight}
				alignSelf={alignSelf}
				style={{ opacity: pulseAnim }}
			>
				{children}
			</LoadElement>
		);
	}
}

export const LoadingStoreDataElement = styled.div`
	width: 100%;
	height: auto;
	background-color: #ffbdcb;
`;

const LoadCardContainer = withEhStates(
	styled.div`
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		padding-horizontal: 20px;
		width: 100%;
		margin-top: 16px;
	`
);

const LoadCard = withEhStates(
	styled(Animated.View)`
		width: ${({ pw }) => pw(91)};
		max-width: 350px;
		height: ${({ isPortrait, pw, ph }) => pw(60)};
		max-height: 450px;
		background-color: rgb(210, 210, 210);
		/* margin-horizontal: 10%; */
		margin-vertical: 10%;
	`,
	sDimensions,
	sTheme
);

const LoadingFeaturedText = withEhStates(
	styled.h2`
		font-weight: 600;
		font-family: ${({ theme }) => theme.colors.font};
		color: ${({ theme }) => theme.colors.aBlack};
	`,
	sTheme
);

export function LoadingCard({ qpie }) {
	const pulseAnim = useRef(new Animated.Value(0.6)).current;

	useEffect(() => {
		animationHandler(pulseAnim, 0.2, 0.6).start();
	}, []);

	return (
		<LoadCardContainer>
			<LoadingFeaturedText>Featured Items</LoadingFeaturedText>
			<LoadCard style={{ opacity: pulseAnim }} />
		</LoadCardContainer>
	);
}
