import styled from "styled-components";
import { ProfileSvg } from "../lib/svgs";
import { LoadingElement } from "./LoadingElement";

const ProfileContainer = styled.div`
	width: 80%;
	height: 200px;
	display: flex;
	flex-direction: row;
	border: 2px solid;
	border-radius: 6px;
	margin-top: 16px;
`;

const CustLoadDivTitle = styled.div`
	width: ${({ width }) => width || "80%"};
	height: ${({ height }) => height || "40px"};
	background-color: gray;
`;

function CustLoadItem({ width, height, marginTop }) {
	return (
		<LoadingElement
			style={{ marginTop: 8 }}
			paddingTop={8}
			width="200px"
			height="40px"
			marginBottom={8}
			marginRight={8}
			hasValue={false}
		>
			<CustLoadDivTitle width={width} height={height} />
		</LoadingElement>
	);
}

const ProfilePicSection = styled.div`
	display: flex;
	width: 20%;
	height: 100%;
	justify-content: center;
	align-items: center;
	border-right: 1px solid;
`;
const TextSection = styled.div`
	display: flex;
	flex-direction: column;
	margin: 8px;
	width: 80%;
`;
const Spacer = styled.div`
	height: ${({ height }) => height || "8px"};
`;

export function LoadExample({}) {
	return (
		<ProfileContainer>
			<ProfilePicSection>
				<ProfileSvg size={80} />
			</ProfilePicSection>
			<TextSection>
				<CustLoadItem width="90%" />
				<Spacer height="16px" />
				<CustLoadItem width="75%" height="24px" marginTop={8} />
				<Spacer />
				<CustLoadItem width="60%" height="20px" marginTop={8} />
				<Spacer />
				<CustLoadItem width="50%" height="16px" marginTop={8} />
				<Spacer />
				<CustLoadItem width="50%" height="16px" marginTop={8} />
				<Spacer />
				<CustLoadItem width="50%" height="16px" marginTop={8} />
			</TextSection>
		</ProfileContainer>
	);
}
