import "./App.css";
import VerticalAnswerSlider, { DUMMY_ANSWER_SET } from "./VerticalSlider";
import styled from "styled-components";
import ComponentContainer from "./components/ComponentContainer";
import { TagScanAnimation } from "./components/NfcScanAnim";
import { LoadExample } from "./components/LoadExample";
import { Pressable } from "react-native";
import { useEffect, useState } from "react";

const Container = styled.div`
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
`;

const TextContainer = styled.div`
	width: 100%;
	margin-left: 5%;
`;

const IntroText = styled.p`
	color: #871f78;
	text-align: left;
`;

const AnswerAmtContainer = styled.div`
	width: 40%;
	height: 60px;
	border: 1px solid;
	border-radius: 16px;
	display: flex;
	flex-direction: row;
	background-color: white;
`;

const AnswerItem = styled.div`
	width: 33.3333%;
	height: 100%;
	justify-content: center;
	align-items: center;
	border-right: ${({ isLast }) => (isLast ? "0px" : "1px solid")};
	border-radius: ${({ isLast, isFirst }) =>
		isLast ? "0px 16px 16px 0px" : isFirst ? "16px 0px 0px 16px" : "0px"};
	background-color: ${({ isSelected }) =>
		isSelected ? "#96d197" : "undefined"};
`;

function HookAnswerItem({ text, isLast, index, setState, state, isFirst }) {
	return (
		<AnswerItem isLast={isLast} isSelected={index === state} isFirst={isFirst}>
			<Pressable onPress={() => setState(index)}>
				<h4>{text}</h4>
			</Pressable>
		</AnswerItem>
	);
}

function App() {
	//0, 1, or 2.
	const [selectedVertSliderCount, setSelectedVertSliderCount] = useState(1);

	// useEffect(() => {}, [selectedVertSliderCount]);

	return (
		<Container className="App">
			<h1>React Native Web Portfolio</h1>
			<TextContainer>
				<IntroText>
					Hi, I'm Reno. Below are some of the React (Native) components I have
					made in the past 1.5 years.
				</IntroText>
				<IntroText>
					17-09: Working on extracting components from app work to this
					portfolio. <br />
					Next items: selector for # of answers on vertical slider and animation
					components. <br />
					Should be online by 20-09.
				</IntroText>
			</TextContainer>
			<ComponentContainer
				title={"React Native + React Native web Answer Slider"}
			>
				{/* <h4> answer amt selector</h4>
				<AnswerAmtContainer>
					<HookAnswerItem
						text="3"
						index={0}
						setState={setSelectedVertSliderCount}
						state={selectedVertSliderCount}
						isFirst={true}
					/>
					<HookAnswerItem
						text="5"
						index={1}
						setState={setSelectedVertSliderCount}
						state={selectedVertSliderCount}
					/>
					<HookAnswerItem
						text="7"
						index={2}
						setState={setSelectedVertSliderCount}
						state={selectedVertSliderCount}
						isLast={true}
					/>
				</AnswerAmtContainer> */}
				<h3>How likely are you to recommend this product?</h3>

				<VerticalAnswerSlider
					answerArray={DUMMY_ANSWER_SET[selectedVertSliderCount]}
				/>
			</ComponentContainer>
			<ComponentContainer title={"NFC Scan Animation (Android)"}>
				<TagScanAnimation />
			</ComponentContainer>
			<ComponentContainer title={"Twitter-style loading animation"}>
				<LoadExample />
			</ComponentContainer>
		</Container>
	);
}

export default App;
