import { withEhState } from "@foursk/eh-react-hooks";
import { Animated, Easing, Linking, Platform } from "react-native";

/**
 *
 * @param {boolean} boolVal
 */
export const NOT = (boolVal) => !boolVal;

export const CURRENCIES = (currency) => {
	switch (currency) {
		case "ILS":
		default:
			return "";
		case "EUR":
			return "";
		case "USD":
			return "$";
	}
};

export function openPhone(phone_number) {
	if (!phone_number) return null;
	const temp_number = Platform.select({
		ios: `telprompt:${phone_number}`,
		android: `tel:${phone_number}`,
		default: `tel:${phone_number}`,
	});
	Linking.openURL(temp_number);
}

export function openMap(lat, long) {
	const scheme = Platform.select({ ios: "maps:0,0?q=", android: "geo:0,0?q=" });
	const latLng = `${lat},${long}`;
	const label = "label here";
	const url = Platform.select({
		ios: `${scheme}${label}@${latLng}`,
		android: `${scheme}${latLng}(${label})`,
	});
	Linking.openURL(url);
}

export const getCurrentDay = () => new Date().getDay();
export const DAY_ARRAY = ["sun", "mon", "tue", "wed", "thu", "fri", "sat"];
export const getCurrentDayStr = () => DAY_ARRAY[getCurrentDay()];

export function getDayData(data) {
	if (!data) return null;
	return [data.find((x) => (x.start_day = DAY_ARRAY[getCurrentDay()]))];
}

export function getTodaysOpeningTimes(opening_times) {
	const { open_time, close_time } = opening_times[getCurrentDay()];
	const returns = { open_time, close_time };
	Object.entries(returns).forEach(
		([key, val]) => (returns[key] = val.substring(0, 5))
	);
	return returns;
}

/**
 * @template T
 * @param {T} comp
 * @param  {...any} ehStates
 * @returns {T}
 */
export function withEhStates(comp, ...ehStates) {
	let withed = comp;
	ehStates.forEach((ehstate) => (withed = withEhState(ehstate, withed)));
	return withed;
}

export const isHexValid = (str) => {
	const legend = "0123456789ABCDEF";
	for (let i = 0; i < str.length; i++) {
		if (legend.includes(str[i])) {
			continue;
		}
		return false;
	}
	return true;
};

/**
 * @template T
 * @param {T[]} list
 * @param {string} mapBy
 * @returns {{[key: string]: T}}
 */
export function objectify(list, mapBy) {
	return Object.assign({}, ...list.map((e) => ({ [e[mapBy]]: e })));
}

function byteToHex(byte) {
	return ("0" + (byte & 0xff).toString(16)).slice(-2);
}

export function toHexString(byteArray) {
	return Array.from(byteArray, byteToHex).join("");
}

export function timedAnimation(anim, toVal, dur, easing) {
	return Animated.timing(anim, {
		toValue: toVal,
		duration: dur,
		useNativeDriver: true,
		easing: easing ? Easing.linear : Easing.inOut(Easing.ease),
	});
}

export function gradientLooper(anim, toVal, dur) {
	timedAnimation(anim, -toVal, dur, true).start(() => {
		anim.setValue(0);
		gradientLooper(anim, toVal, dur);
	});
}
